export default "@import '../../../../themes/src/default.css';\n\n:root {\n    --list-marker-color: var(--color-light-text-primary);\n    --list-item-margin: var(--gap-s);\n}\n\n.item {\n    margin-top: var(--list-item-margin);\n    display: flex;\n\n    &:first-child {\n        margin-top: 0;\n    }\n\n    &.reversed:first-child {\n        margin-top: var(--list-item-margin);\n    }\n\n    &.reversed:last-child {\n        margin-top: 0;\n    }\n\n    &.unorderedItem {\n        display: list-item;\n    }\n}\n\n.slot {\n    min-width: 20px;\n    display: flex;\n    box-sizing: border-box;\n    justify-content: start;\n    padding-right: var(--gap-2xs);\n    flex-shrink: 0;\n\n    &.defaultColor {\n        color: var(--list-marker-color);\n    }\n\n    &.disc {\n        justify-content: center;\n        padding: 0 var(--gap-2xs);\n    }\n}\n\n.caption {\n    margin-top: var(--gap-3xs);\n}\n";